import * as React from "react";
import {Component} from "react";
import './pubFoot.scss'


type PageOwnProps = {}

type PageState = {}


export default class PubFoot extends Component <PageOwnProps, PageState> {
    public render() {
        return (
            <div className={'pub-foot-warp'}>
                <div className={'pub-foot'}>
                    <p></p>
                    <ol>
                        <li>www.yuandingbang.cn</li>
                        <li></li>
                        <li></li>
                    </ol>
                </div>
            </div>
        )
    }
}





